@font: 'Comfortaa', cursive;
@orange: #ffb400;
.reset();
.wrapper{
    .wrapper();
}
.bold(){
    font-weight: 700;
}
body{
    font-family: 'Roboto', sans-serif;
    background: url('../img/memphis-mini-dark.png');
    font-size: 10px;
}

.center{
    padding: 0 3rem;
}

.center-margin{
    margin: 0 1rem;
}

.html{
    color: #f47933;
}

.css{
    color: #008fd5;
}

.js{
    color: yellow;
}

.prev, .next{
    background: transparent;
    border: none;
    cursor: pointer;
    color: #fff;
    outline: none;
}

.prev:hover, .next:hover{
    color: @orange;
    transition: 0.4s;
}

.prev{
    position: absolute;
    top: 25%;
    left: 1%;
    transform: translateY(-50%);
    z-index: 99;
    border: none;
    cursor: pointer;
    font-size: 2rem;
}

.next{
    position: absolute;
    top: 25%;
    right: 1%;
    transform: translateY(-50%);
    z-index: 99;
    border: none;
    cursor: pointer;
    font-size: 2rem;
}

