body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1170px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

body {
  font-family: 'Roboto', sans-serif;
  background: url('../img/memphis-mini-dark.png');
  font-size: 10px;
}

.center {
  padding: 0 3rem;
}

.center-margin {
  margin: 0 1rem;
}

.html {
  color: #f47933;
}

.css {
  color: #008fd5;
}

.js {
  color: yellow;
}

.prev,
.next {
  background: transparent;
  border: none;
  cursor: pointer;
  color: #fff;
  outline: none;
}

.prev:hover,
.next:hover {
  color: #ffb400;
  transition: 0.4s;
}

.prev {
  position: absolute;
  top: 25%;
  left: 1%;
  transform: translateY(-50%);
  z-index: 99;
  border: none;
  cursor: pointer;
  font-size: 2rem;
}

.next {
  position: absolute;
  top: 25%;
  right: 1%;
  transform: translateY(-50%);
  z-index: 99;
  border: none;
  cursor: pointer;
  font-size: 2rem;
}

/*Header*/

.header {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  padding-top: 2rem;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(16.666666666666668% - 30px);
}

.header-nav {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
  width: calc(83.33333333333334% - 30px);
  justify-content: flex-end;
  align-items: center;
  font-family: 'Comfortaa', cursive;
}

.header-nav-item {
  color: #ffffff;
  font-size: 1.4rem;
  font-weight: 700;
  padding-left: 1rem;
  text-decoration: none;
}

.header-nav-item:hover {
  color: #ffb400;
  transition: 0.4s;
}

.burger-menu {
  display: none;
}

.burger-menu-logo {
  padding-top: 3rem;
  margin-left: 2rem;
}

/* menu button */

.menuBtn {
  height: 3rem;
  width: 3rem;
  position: absolute;
  right: 4rem;
  top: 3.3rem;
  z-index: 101;
}

.menuBtn > span {
  background-color: #ffffff;
  border-radius: 1px;
  height: 2px;
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -1px 0 0 -15px;
  transition: height 100ms;
}

.menuBtn > span:after,
.menuBtn > span:before {
  content: '';
  background-color: #fff;
  border-radius: 1px;
  height: 2px;
  width: 100%;
  position: absolute;
  left: 50%;
  margin-left: -24px;
  transition: all 200ms;
}

.menuBtn > span:after {
  top: -7px;
}

.menuBtn > span:before {
  bottom: -7px;
}

.menuBtn.act > span {
  height: 0;
}

.menuBtn.act > span:after,
.menuBtn.act > span:before {
  background-color: #fff;
  top: 1px;
}

.menuBtn.act > span:after {
  transform: rotate(45deg);
}

.menuBtn.act > span:before {
  transform: rotate(-45deg);
}

.menuBtn:hover span,
.menuBtn:hover span::before,
.menuBtn:hover span::after {
  background: #ffb400;
  transition: 0.4s;
}

/* main menu block */

.mainMenu {
  background-color: #000;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  display: table;
  text-align: center;
  opacity: 0;
  transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform: scale(0);
}

.mainMenu.act {
  opacity: 0.95;
  transform: scale(1);
}

.mainMenu.act ul li {
  opacity: 1;
  transform: translateX(0);
}

.mainMenu ul {
  display: table-cell;
  vertical-align: middle;
}

.mainMenu li {
  padding: 8px 0;
  transition: all 400ms 510ms;
  opacity: 0;
}

.mainMenu li:nth-child(odd) {
  transform: translateX(30%);
}

.mainMenu li:nth-child(even) {
  transform: translateX(-30%);
}

.mainMenu li:last-child {
  transform: none;
}

.mainMenu a {
  font-family: 'Comfortaa', cursive;
  color: #fff;
  display: inline-block;
  font-size: 2.5rem;
  text-decoration: none;
}

.mainMenu a:hover {
  color: #ffb400;
  transition: 0.4s;
}

/*About me*/

.about {
  margin-top: 5rem;
}

.about-autor {
  text-align: center;
}

.about-autor-prof {
  color: #fff;
  display: inline;
  font-size: 3rem;
  padding: 0 2rem;
}

.about-autor-name {
  text-align: center;
  color: #fff;
  font-size: 6rem;
  font-weight: 700;
}

.about-autor-nickname {
  text-align: center;
  font-size: 2rem;
  color: #ffffff;
}

.about-autor-nickname span {
  color: #ffb400;
}

.line {
  color: #fff;
  font-size: 2rem;
}

.underline {
  margin: 0 auto;
  margin-top: 1rem;
  background: #fff;
  height: 0.5rem;
  width: 15rem;
  border-radius: 2rem;
}

.about-autor-skills {
  margin-top: 1rem;
}

.about-autor-skills-title {
  text-align: center;
  color: #fff;
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: 700;
}

.about-autor-skills-items {
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.about-autor-skills-item {
  color: #fff;
  font-size: 1.4rem;
}

.down-link {
  margin-top: 5rem;
  text-align: center;
}

.down-link a {
  font-size: 3rem;
  color: #fff;
}

.down-link a:hover {
  color: #ffb400;
  transition: 0.4s;
}

/*Services*/

.services-title {
  color: #fff;
  font-size: 3rem;
  text-align: center;
  font-family: 'Comfortaa', cursive;
  font-weight: 700;
  margin-top: 10rem;
}

.services-slider {
  margin-top: 6rem;
  outline: none;
}

.services-slider-item {
  text-align: center;
  outline: none;
}

.services-slider-img {
  margin: 0 auto;
}

.services-slider-name {
  margin-top: 1.5rem;
  color: #fff;
  font-size: 1.4rem;
}

/*Portfolio*/

.portfolio-title {
  color: #fff;
  font-size: 3rem;
  text-align: center;
  font-family: 'Comfortaa', cursive;
  font-weight: 700;
  margin-top: 6rem;
}

.portfolio-cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 5rem 0;
}

.portfolio-card {
  width: calc(33.333333333333336% - 30px);
  border-radius: 1rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.portfolio-card-img img {
  width: 100%;
  border-radius: 10px 10px 0 0;
}

.portfolio-card-descr {
  margin-top: -3px;
  padding: 1rem 2rem;
  background: #1a1a1a;
  border-radius: 0 0 10px 10px;
}

.portfolio-card-type {
  font-size: 1rem;
  color: #fff;
}

.portfolio-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
}

.portfolio-card-name {
  font-size: 1.2rem;
  font-weight: 700;
  color: #fff;
}

.portfolio-card-view a {
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 700;
  color: #fff;
}

.portfolio-card-view a:hover {
  color: #ffb400;
  transition: 0.4s;
}

/*Contacts*/

.contacts-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 3.5rem;
}

.contacts-title {
  color: #fff;
  font-size: 3rem;
  text-align: center;
  font-family: 'Comfortaa', cursive;
  font-weight: 700;
  margin-top: 8rem;
}

.contacts-subtitle {
  color: #a6a6a6;
  font-size: 1.6rem;
  text-align: center;
  margin-top: 2rem;
}

.contacts-items {
  width: calc(50% - 30px);
  margin-top: 4rem;
}

.contacts-item {
  color: #fff;
  font-size: 1.2rem;
  padding-bottom: 2rem;
}

.contacts-item a {
  color: #fff;
  text-decoration: none;
}

.contacts-item a:hover {
  color: #ffb400;
  transition: 0.4s;
}

.contacts-item-type {
  color: #fff;
  font-size: 1.4rem;
  padding-bottom: 2.5rem;
  font-weight: 700;
}

.contacts-freelance-item {
  color: #fff;
  font-size: 1.2rem;
  padding-bottom: 2rem;
}

.contacts-freelance-item a {
  color: #fff;
  text-decoration: none;
}

.contacts-freelance-item a:hover {
  color: #ffb400;
  transition: 0.4s;
}

.contacts-freelance-type {
  color: #fff;
  font-size: 1.4rem;
  padding-bottom: 2.5rem;
  font-weight: 700;
}

.contacts-freelance {
  width: calc(50% - 30px);
  margin-top: 4rem;
  text-align: right;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .portfolio-card {
    width: calc(50% - 30px);
  }

  .portfolio-card {
    padding-bottom: 1.5rem;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .portfolio-card {
    width: calc(66.66666666666667% - 30px);
  }

  .contacts-container {
    justify-content: center;
  }

  .contacts-items {
    width: calc(100% - 30px);
  }

  .contacts-freelance {
    width: calc(100% - 30px);
  }

  .header {
    display: none;
  }

  .burger-menu {
    display: block;
  }

  .about-autor-prof {
    font-size: 2rem;
  }

  .about-autor-name {
    font-size: 5rem;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .about-autor-skills-items {
    justify-content: center;
  }

  .about-autor-skills-item {
    width: calc(100% - 30px);
  }

  .portfolio-card {
    width: calc(91.66666666666667% - 30px);
  }

  .about-autor-prof {
    font-size: 1.6rem;
    padding: 0 0.5rem;
  }

  .about-autor-name {
    font-size: 3.5rem;
  }

  .about-autor-nickname {
    font-size: 1.8rem;
  }

  .about-autor-skills-title {
    font-size: 2.5rem;
  }

  .about-autor-skills-item {
    font-size: 1.4rem;
    padding-bottom: 1rem;
  }

  .line {
    font-size: 1.2rem;
  }

  .underline {
    width: 12rem;
  }

  .menuBtn {
    right: 1.7rem;
  }

  .burger-menu-logo {
    margin-left: 1rem;
  }

  .mainMenu a,
  .suBtn {
    font-size: 2rem;
  }

  .portfolio-title {
    font-size: 2.5rem;
  }

  .contacts-title {
    font-size: 2.5rem;
  }

  .contacts-subtitle {
    font-size: 1.2rem;
  }

  .contacts-items,
  .contacts-freelance {
    text-align: center;
  }

  .contacts-item {
    font-size: 1rem;
  }

  .contacts-freelance {
    margin-top: 2rem;
  }

  .contacts-freelance-item {
    font-size: 1rem;
  }

  .portfolio-card-descr {
