    padding-right: 5px;
  }
}

@media screen and (max-width: 374px) {
  .about {
    margin-top: 3rem;
  }

  .about-autor-prof {
    font-size: 1.6rem;
    padding: 0 0.5rem;
  }

  .about-autor-name {
    padding: 1.2rem 0;
    font-size: 2.8rem;
  }

  .about-autor-nickname {
    font-size: 1.8rem;
  }

  .about-autor-skills-title {
    font-size: 2.5rem;
  }

  .about-autor-skills-item {
    font-size: 1.4rem;
    padding-bottom: 1rem;
  }
}