@import 'smart-grid.less';
@import 'settings.less';
/*Header*/
.header{
    .row-flex();
    padding-top: 2rem;
    justify-content: space-between;
    align-items: center;
}
.header-logo{
    .col();
    .size(2);
}
.header-nav{
    .row-flex();
    .col();
    .size(10);
    justify-content: flex-end;
    align-items: center;
    font-family: @font;
    &-item{
        color: #ffffff;
        font-size: 1.4rem;
        .bold();
        padding-left: 1rem;
        text-decoration: none;
    }

    &-item:hover{
        color: @orange;
        transition: 0.4s;
    }
}

.burger-menu {
    display: none;
}

.burger-menu-logo {
    padding-top: 3rem;
    margin-left: 2rem;

}

/* menu button */
.menuBtn {
    height: 3rem;
    width: 3rem;
    position: absolute;
    right: 4rem;
    top: 3.3rem;
    z-index: 101;

    >span {
        background-color: #ffffff;
        border-radius: 1px;
        height: 2px;
        width: 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -1px 0 0 -15px;
        transition: height 100ms;

        &:after,
        &:before {
            content: '';
            background-color: #fff;
            border-radius: 1px;
            height: 2px;
            width: 100%;
            position: absolute;
            left: 50%;
            margin-left: -24px;
            transition: all 200ms;
        }

        &:after {
            top: -7px;
        }

        &:before {
            bottom: -7px;
        }
    }

    &.act {
        >span {
            height: 0;

            &:after,
            &:before {
                background-color: #fff;
                top: 1px;
            }

            &:after {
                transform: rotate(45deg);
            }

            &:before {
                transform: rotate(-45deg);
            }
        }
    }
}

.menuBtn:hover{
    span, span::before, span::after{
        background: @orange;
        transition: 0.4s;
    }
}

/* main menu block */
.mainMenu {
    background-color: #000;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100%;
    width: 100%;
    display: table;
    text-align: center;
    opacity: 0;
    transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transform: scale(0);

    &.act {
        opacity: 0.95;
        transform: scale(1);

        ul li {
            opacity: 1;
            transform: translateX(0);
        }
    }

    ul {
        display: table-cell;
        vertical-align: middle;
    }

    li {
        padding: 8px 0;
        transition: all 400ms 510ms;
        opacity: 0;

        &:nth-child(odd) {
            transform: translateX(30%);
        }

        &:nth-child(even) {
            transform: translateX(-30%);
        }

        &:last-child {
            transform: none;
        }
    }

    a {
        font-family: @font;
        color: #fff;
        display: inline-block;
        font-size: 2.5rem;
        text-decoration: none;
    }

    a:hover{
        color: @orange;
        transition: 0.4s;
    }
    

}
/*About me*/
.about{
    margin-top: 5rem;
}
.about-autor{
    text-align: center;
    &-prof{
        color: #fff;
        display: inline;
        font-size: 3rem;
        padding: 0 2rem;
    }

    &-name{
        text-align: center;
        color: #fff;
        font-size: 6rem;
        .bold();
    }

    &-nickname{
        text-align: center;
        font-size: 2rem;
        color: #ffffff;

        span{
            color: @orange;
        }
    }
}


.line{
    color: #fff;
    font-size: 2rem;
}

.underline{
    margin: 0 auto;
    margin-top: 1rem;
    background: #fff;
    height: 0.5rem;
    width: 15rem;
    border-radius: 2rem;
}

.about-autor-skills{
    
    margin-top: 1rem;
    &-title{
        text-align: center;
        color: #fff;
        font-size: 3rem;
        text-transform: uppercase;
        .bold();
    }
    &-items{
        margin-top: 1rem;
        display: flex;
        flex-wrap: wrap;
        .justify-content(center);
        .justify-content-xs(center);
        text-align: center;
    }

    &-item{
        .size-xs(12);
        color: #fff;
        font-size: 1.4rem;
        
    }
}

.down-link{
    margin-top: 5rem;
    text-align: center;
    
    a{
        font-size: 3rem;
        color: #fff;
    }

    a:hover{
        color: @orange;
        transition: 0.4s;
    }
}

/*Services*/
.services{
    &-title{
        color: #fff;
        font-size: 3rem;
        text-align: center;
        font-family: @font;
        .bold();
        margin-top: 10rem;
    }
    &-slider{
        margin-top: 6rem;
        outline: none;
        &-item{
            text-align: center;
            outline: none;
        }
        &-img{
            margin: 0 auto;
        }
        &-name{
            margin-top: 1.5rem;
            color: #fff;
            font-size: 1.4rem;
        }
    }

}

/*Portfolio*/
.portfolio-title{
    color: #fff;
    font-size: 3rem;
    text-align: center;
    font-family: @font;
    .bold();
    margin-top: 6rem;
}

.portfolio-cards {
    display: flex;
    justify-content: space-around   ;
    flex-wrap: wrap;
    margin: 5rem 0;
}

.portfolio-card {
    .size(4);
    .size-md(6);
    .size-sm(8);
    .size-xs(11);

    border-radius: 1rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);

    &-img img {
        width: 100%;
        border-radius: 10px 10px 0 0;
    }

    &-descr {
        margin-top: -3px;
        padding: 1rem 2rem;
        background: #1a1a1a;
        border-radius: 0 0 10px 10px;
    }

    &-type {
        font-size: 1rem;
        color: #fff;
    }

    &-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 1rem;
    }

    &-name {
        font-size: 1.2rem;
        .bold();
        color: #fff;
    }

    &-view a {
        text-decoration: none;
        font-size: 1.2rem;
        .bold();
        color: #fff;
    }

    &-view a:hover {
        color: @orange;
        transition: 0.4s;
    }

}

/*Contacts*/
.contacts-container{
    display: flex;
    .justify-content(space-between);
    .justify-content-sm(center);
    flex-wrap: wrap;
    padding-bottom: 3.5rem;
}
.contacts{
    
    &-title{
        color: #fff;
        font-size: 3rem;
        text-align: center;
        font-family: @font;
        .bold();
        margin-top: 8rem;
    }

    &-subtitle{
        color: #a6a6a6;
        font-size: 1.6rem;
        text-align: center;
        margin-top: 2rem;
    }

    &-items{
        .size(6);
        .size-sm(12);
        margin-top: 4rem;
    }

    &-item{
        color: #fff;
        font-size: 1.2rem;
        padding-bottom: 2rem;
        a{
            color: #fff;
            text-decoration: none;
        }

        a:hover{
            color: @orange;
            transition: 0.4s;
        }
    }

    &-item-type{
        color: #fff;
        font-size: 1.4rem;
        padding-bottom: 2.5rem;
        .bold();
    }

    &-freelance-item{
        color: #fff;
        font-size: 1.2rem;
        padding-bottom: 2rem;
        a{
            color: #fff;
            text-decoration: none;
        }

        a:hover{
            color: @orange;
            transition: 0.4s;
        }
    }

    &-freelance-type{
        color: #fff;
        font-size: 1.4rem;
        padding-bottom: 2.5rem;
        .bold();
    }
}

.contacts-freelance{
    .size(6);
    .size-sm(12);
    margin-top: 4rem;
    text-align: right;
}



@media screen and (max-width:992px){
    .portfolio-card{
        padding-bottom: 1.5rem;
    }
}

@media screen and (max-width:720px){
    .header{
        display: none;
    }

    .burger-menu{
        display: block;
    }

    .about-autor{
        &-prof{
            font-size: 2rem;
        }

        &-name{
            font-size: 5rem;
        }
    }
}

@media screen and (max-width:576px){
    .about-autor{
        &-prof{
            font-size: 1.6rem;
            padding: 0 0.5rem;
        }

        &-name{
            font-size: 3.5rem;
        }

        &-nickname{
            font-size: 1.8rem;
        }

        &-skills-title{
            font-size: 2.5rem;
        }
 
        
        &-skills-item{
            font-size: 1.4rem;
            padding-bottom: 1rem;
        }
    }
    .line{
        font-size: 1.2rem;  
    }
    .underline{
        width: 12rem;
    }
    .menuBtn{
        right: 1.7rem;
    }
    .burger-menu-logo{
        margin-left: 1rem;
    }
    .mainMenu a, .suBtn{
        font-size: 2rem;
    }

    .portfolio-title{
        font-size: 2.5rem;
    }
    .contacts{
        &-title{
            font-size: 2.5rem;
        }

        &-subtitle{
            font-size: 1.2rem;
        }

        &-items, &-freelance{
            text-align: center;
        }

        &-item{
            font-size: 1rem;
        }

        &-freelance{
            margin-top: 2rem;
        }

        &-freelance-item{
            font-size: 1rem;
        }
    }


    .portfolio-card-descr{
        padding: 1rem 1rem;
    }

}

@media screen and (max-width:374px){
    .about{
        margin-top: 3rem;
    }
    .about-autor{
        &-prof{
            font-size: 1.6rem;
            padding: 0 0.5rem;
        }

        &-name{
            padding: 1.2rem 0;
            font-size: 2.8rem;
        }

        &-nickname{
            font-size: 1.8rem;
        }

        &-skills-title{
            font-size: 2.5rem;
        }
 
        
        &-skills-item{
            font-size: 1.4rem;
            padding-bottom: 1rem;
        }
    }
}
